import React, { Fragment, useState } from "react";
import { Loading, AppBar, Launcher } from "components";
import { BrowserRouter as Router, Link } from "react-router-dom";

const Home = (props) => {
  const [loading] = useState(false);

  if (!!loading) {
    return <Loading />;
  }

  return (
    <Fragment>
      <Launcher logo="logo192.png" />
    </Fragment>
  );
};

export default Home;
